
import XTable from 'vux/src/components/x-table/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {XTable, Group, Cell},
data() {
return {
listUrl: '/api/gdekhback/special/goods_list',
list: [],
aid:'',
pid:'',
}
},
mounted() {
this.aid = this.$route.query.aid;
this.pid = sessionStorage.getItem('pid');
this.getData()
},
methods: {
goHome(){
this.$router.push('/activityYiqing')
},
toDetail(id){
this.$router.push({
path: '/details',
query: {
id: id
}
});
},
getData() {
this.$vux.loading.show({
text: '加载中...'
})
this.$http.post(this.listUrl, {
pid: this.pid,
aid: this.aid
}).then(res => {
this.$vux.loading.hide();
if (res.data.status === 0) {
this.list = res.data.info;
} else {
this.$vux.toast.show({text: res.data.info, type: 'warn'})
}
}).catch(err => {
this.$vux.loading.hide();
this.$vux.toast.show({text: err, type: 'warn'})
})
}
}
}



